class TabStyleThree extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tab1: 0,
            tab2: 0,
            tab3: 0,
            tab4: 0,
            isOpen: false,
            show: false,
            video: null,
        };
    }
    render() {
        const { column } = this.props;
        const { tab1, tab2, tab3, show, isOpen, video } = this.state;

        const handleClose = () => this.setState({ show: false, video: null });
        const handleShow = video => this.setState({ show: true, video: video });
        return (
            <div>
                <Tabs>
                    <div className="row text-center">
                        <div className="col-lg-12">
                            <div className="tablist-inner">
                                <TabList className="pv-tab-button text-center mt--0">
                                    <Tab>
                                        <span></span>
                                    </Tab>
                                    <Tab>
                                        <span></span>
                                    </Tab>
                                    <Tab>
                                        <span></span>
                                    </Tab>
                                </TabList>
                            </div>
                        </div>
                    </div>

                    <TabPanel className="row row--35">
                        {TabOne.map((value, index) => (
                            <div className={`${column}`} key={index}>
                                {!TabOne[index]?.video && isOpen && (
                                    <Lightbox
                                        mainSrc={TabOne[tab1].bigImage}
                                        nextSrc={TabOne[(tab1 + 1) % TabOne.length]}
                                        prevSrc={TabOne[(tab1 + TabOne.length - 1) % TabOne.length]}
                                        onCloseRequest={() => this.setState({ isOpen: false })}
                                        onMovePrevRequest={() =>
                                            this.setState({
                                                tab1: (tab1 + TabOne.length - 1) % TabOne.length,
                                            })
                                        }
                                        onMoveNextRequest={() =>
                                            this.setState({
                                                tab1: (tab1 + 1) % TabOne.length,
                                            })
                                        }
                                        imageLoadErrorMessage="Image Loading ..."
                                        enableZoom={false}
                                    />
                                )}
                                <div className="item-portfolio-static">
                                    <div
                                        onClick={() => {
                                            if (TabOne[index]?.video) {
                                                handleShow(TabOne[index]?.video);
                                            } else {
                                                this.setState({ isOpen: true, tab1: index });
                                            }
                                        }}
                                    >
                                        <div className="portfolio-static">
                                            <div className="thumbnail-inner">
                                                <div className="thumbnail">
                                                    <a href="#portfolio-details">
                                                        <img src={value.bigImage} alt="Portfolio Images" />
                                                    </a>
                                                </div>
                                            </div>
                                            <div className="content">
                                                <div className="inner">
                                                    <p>{value.category}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </TabPanel>

                    <TabPanel className="row row--35">
                        {TabTwo.map((value, index) => (
                            <div className={`${column}`} key={index}>
                                {!TabTwo[index]?.video && isOpen && (
                                    <Lightbox
                                        mainSrc={TabTwo[tab2].bigImage}
                                        nextSrc={TabTwo[(tab2 + 1) % TabTwo.length]}
                                        prevSrc={TabTwo[(tab2 + TabTwo.length - 1) % TabTwo.length]}
                                        onCloseRequest={() => this.setState({ isOpen: false })}
                                        onMovePrevRequest={() =>
                                            this.setState({
                                                tab2: (tab2 + TabTwo.length - 1) % TabTwo.length,
                                            })
                                        }
                                        onMoveNextRequest={() =>
                                            this.setState({
                                                tab2: (tab2 + 1) % TabTwo.length,
                                            })
                                        }
                                        imageLoadErrorMessage="Image Loading ..."
                                        enableZoom={false}
                                    />
                                )}
                                <div className="item-portfolio-static">
                                    <div
                                        onClick={() => {
                                            if (TabTwo[index]?.video) {
                                                handleShow(TabTwo[index]?.video);
                                            } else {
                                                this.setState({ isOpen: true, tab2: index });
                                            }
                                        }}
                                    >
                                        <div className="portfolio-static">
                                            <div className="thumbnail-inner">
                                                <div className="thumbnail">
                                                    <a href="#portfolio-details">
                                                        <img src={value.bigImage} alt="Portfolio Images" />
                                                    </a>
                                                </div>
                                            </div>
                                            <div className="content">
                                                <div className="inner">
                                                    <p>{value.category}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </TabPanel>

                    <TabPanel className="row row--35">
                        {TabThree.map((value, index) => (
                            <div className={`${column}`} key={index}>
                                {!TabThree[index]?.video && isOpen && (
                                    <Lightbox
                                        mainSrc={TabThree[tab3].bigImage}
                                        nextSrc={TabThree[(tab3 + 1) % TabThree.length]}
                                        prevSrc={TabThree[(tab3 + TabThree.length - 1) % TabThree.length]}
                                        onCloseRequest={() => this.setState({ isOpen: false })}
                                        onMovePrevRequest={() =>
                                            this.setState({
                                                tab3: (tab3 + TabThree.length - 1) % TabThree.length,
                                            })
                                        }
                                        onMoveNextRequest={() =>
                                            this.setState({
                                                tab3: (tab3 + 1) % TabThree.length,
                                            })
                                        }
                                        imageLoadErrorMessage="Image Loading ..."
                                        enableZoom={false}
                                    />
                                )}

                                <div className="item-portfolio-static">
                                    <div
                                        onClick={() => {
                                            if (TabThree[index]?.video) {
                                                handleShow(TabThree[index]?.video);
                                            } else {
                                                this.setState({ isOpen: true, tab3: index });
                                            }
                                        }}
                                    >
                                        <div className="portfolio-static">
                                            <div className="thumbnail-inner">
                                                <div className="thumbnail">
                                                    <a href="#portfolio-details">
                                                        <img src={value.bigImage} alt="Portfolio Images" />
                                                    </a>
                                                </div>
                                            </div>
                                            <div className="content">
                                                <div className="inner">
                                                    <p>{value.category}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </TabPanel>
                </Tabs>
                <Modal show={show} onHide={handleClose} size="xl">
                    <Modal.Header closeButton>{/* <Modal.Title>Modal heading</Modal.Title> */}</Modal.Header>
                    <Modal.Body>
                        <ReactPlayer url={video} width="100%" height="100%" controls={true} />
                    </Modal.Body>
                    {/* <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                        <Button variant="primary" onClick={handleClose}>
                            Save Changes
                        </Button>
                    </Modal.Footer> */}
                </Modal>
            </div>
        );
    }
}

export default TabStyleThree;
