import React, { Component } from 'react';
import { VideoTag, videoTagString } from 'react-video-tag';

import { FiChevronUp } from 'react-icons/fi';
import FooterTwo from '../component/footer/FooterTwo';
import Header from '../component/header/HeaderFour';
import Helmet from '../component/common/Helmet';
import Modal from 'react-bootstrap/Modal';
import ModalVideo from 'react-modal-video';
import ReactPlayer from 'react-player';
import ScrollToTop from 'react-scroll-up';
import TabThree from '../elements/tab/TabThree';
import Video1 from '../assets/video/video7.mp4';

const SlideList = [
    {
        textPosition: 'text-left',
        category: '  ',
        title: '  ',
        description: '    .',
        buttonText: '   ',
        buttonLink: '',
    },
];
class DesignerPortfolio extends Component {
    constructor() {
        super();
        this.state = {
            isOpen: false,
            show: false,
            video: Video1,
        };
        this.openModal = this.openModal.bind(this);
    }

    openModal() {
        this.setState({ isOpen: true });
    }

    render() {
        const { show, video } = this.state;
        const handleClose = () => this.setState({ show: false });
        const handleShow = () => this.setState({ show: true });
        return (
            <div>
                <Helmet pageTitle="Designer Portfolio" />

                <div className="slider-wrapper">
                    {/* Start Single Slide */}
                    {SlideList.map((value, index) => (
                        <div
                            className="slide slider_fixed_height slider-style-3 d-flex align-items-center justify-content-center bg_image bg_image--16 white-overlay-mobile"
                            key={index}
                        >
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className={`inner ${value.textPosition}`}>
                                            {value.category ? (
                                                <span className="theme-gradient font-500">{value.category}</span>
                                            ) : (
                                                ''
                                            )}
                                            {value.title ? (
                                                <h1
                                                    className="title"
                                                    dangerouslySetInnerHTML={{ __html: value.title }}
                                                ></h1>
                                            ) : (
                                                ''
                                            )}
                                            {value.description ? (
                                                <p
                                                    className="description"
                                                    dangerouslySetInnerHTML={{ __html: value.description }}
                                                ></p>
                                            ) : (
                                                ''
                                            )}
                                            {value.buttonText ? (
                                                <div className="slide-btn">
                                                    <a
                                                        className="btn-default"
                                                        onClick={() => {
                                                            handleShow();
                                                        }}
                                                    >
                                                        {value.buttonText}
                                                    </a>
                                                </div>
                                            ) : (
                                                ''
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>

                {/* Start Designer Portfolio  */}
                <div className="designer-portfolio-area ptb--120 bg_color--1">
                    <div className="wrapper plr--70 plr_sm--30 plr_md--30">
                        <TabThree column="col-lg-4 col-md-6 col-sm-6 col-12" />
                    </div>
                </div>
                {/* End Designer Portfolio  */}

                {/* Start Footer Style  */}
                {/* End Footer Style  */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                <Modal show={show} onHide={handleClose} size="lg">
                    <Modal.Header closeButton>{/* <Modal.Title>Modal heading</Modal.Title> */}</Modal.Header>
                    <Modal.Body>
                        <ReactPlayer url={video} width="100%" height="100%" controls={true} />
                    </Modal.Body>
                </Modal>
                {/* End Back To Top */}
            </div>
        );
    }
}

export default DesignerPortfolio;
